.for-what {
  display: flex;
  flex-direction: column;
  align-items: center;
  
  @media (max-width: 1023px) {
    &__wrapper svg {
      width: 100%;
    }
  }
}

.process {
  display: grid;
  grid-template-columns: fit-content(40%) 1fr fit-content(40%);
  justify-content: space-between;
  padding: 51px 130px 51px 66px;
  margin-top: 48px;

  &__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 70px;
    padding-left: 20px;

    svg:nth-child(1) {
      align-self: flex-end;
    }

    h2 {
      margin: 7px 0 9px 0;
      color: #000;
      font-family: 'RG-Standard', sans-serif;
      font-size: 40px;
      font-style: normal;
      font-weight: 700;
      line-height: 120%; /* 48px */
    }

    p {
      margin-top: 7px;
      color: #2E9164;
      text-align: right;
      font-family: "hand3", sans-serif;
      font-size: 24px;
      font-style: normal;
      font-weight: 500;
      line-height: 150%; /* 36px */
      letter-spacing: -0.264px;
    }
  }

  &__img-wrapper {
    position: relative;
    justify-self: center;

    &__notes {
      position: absolute;
      max-width: 318px;
      max-height: 257px;
      bottom: 11px;
      left: -275px;
    }
  }

  &__swrapper {
    p {
      margin: 180px 0 39px 0;
      color: rgba(51, 51, 51, 0.80);
      font-family: "hand4", sans-serif;
      font-size: 24px;
      font-style: normal;
      font-weight: 500;
      line-height: 150%; /* 36px */
      letter-spacing: -0.264px;
    }
  }

  @media (max-width: 1439px) {
    padding: 51px 20px 51px 20px;

    &__swrapper {
      &-man {
        svg {
          width: 100%;
          height: 100%;
        }
      }
    }
  }

  @media (max-width: 1023px) {
    padding: 51px 0;
    grid-template-columns: 1fr;

    &__wrapper {
      padding: 0 13px 0 26px;
      flex-direction: row-reverse;
      column-gap: 5px;

      svg:nth-child(1) {
        align-self: center;
      }

      &-text {
        width: 100%;

        svg {
          width: 100%;
          height: 100%;
        }

        p {
          text-align: left;
        }
      }
    }
  }
}

.client {
