  position: relative;
  align-items: center;

  &__wrapper {
    display: flex;
    flex-direction: column;
    align-items: flex-end;

    svg:nth-child(1) {
      align-self: flex-end;
    }

    h2 {
      margin: 16px 0 14px 0;
      color: #000;
      font-family: "RG-Standard", sans-serif;
      font-size: 40px;
      font-style: normal;
      font-weight: 700;
      line-height: 120%; /* 48px */
    }

    p {
      margin: 7px 0 25px 0;
      color: #2E9164;
      text-align: right;
      font-family: "hand3", sans-serif;
      font-size: 24px;
      font-style: normal;
      font-weight: 500;
      line-height: 150%; /* 36px */
      letter-spacing: -0.264px;
