.team {
  display: grid;
  grid-template-columns: fit-content(40%) 1fr 429px;
  justify-content: space-between;
  padding: 94px 78px 17px 103px;
  margin-top: 23px;

  &__wrapper {
    display: flex;
    flex-direction: column;
    align-items: flex-end;

    h2 {
      margin: 12px 0;
      color: #000;
      text-align: right;
      font-family: "RG-Standard", sans-serif;
      font-size: 40px;
      font-style: normal;
      font-weight: 700;
      line-height: 120%; /* 48px */
    }

    p {
      margin: 7px 0 122px 0;
      color: #2E9164;
      text-align: right;
      font-family: "hand3", sans-serif;
      font-size: 24px;
      font-style: normal;
      font-weight: 500;
      line-height: 150%; /* 36px */
      letter-spacing: -0.264px;
    }

    &-man {
      align-self: flex-start;
    }
  }

  &__img-wrapper {
    justify-self: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 7px;
    margin-right: 60px;
    position: relative;

    svg:nth-child(1) {
      position: absolute;
      left: -62px;
      top: 50%;
      transform: translate(0, -50px);
    }

    svg:nth-child(4) {
      position: absolute;
      right: -62px;
      top: 50%;
      transform: translate(0, -50px);
    }

    img {
      max-width: 368.108px;
      max-height: 677px
    }
  }

  &__swrapper {
    display: grid;
    grid-template-areas:
        "text"
        "img";
    margin-top: 100px;
    position: relative;
    right: 50px;

    p {
      grid-area: text;
      color: rgba(51, 51, 51, 0.80);
      font-family: "hand4", sans-serif;
      font-size: 24px;
      font-style: normal;
      font-weight: 500;
      line-height: 150%; /* 36px */
      letter-spacing: -0.264px;
    }

    &-img {
      grid-area: img;
      justify-self: center;
    }
  }

  @media (max-width: 1439px) {
    padding: 94px 20px 17px 20px;
    grid-template-columns: fit-content(40%) 1fr 250px;

    &__wrapper {
      &-man svg {
        width: 100%;
      }
    }

    &__img-wrapper {
      svg:nth-child(3) {
        width: 100%;
      }
    }

    &__swrapper {
      right: auto;
      &-img svg {
        width: 100%;
      }
    }
  }
}

