      font-size: 40px;
      font-style: normal;
      font-weight: 700;
      line-height: 120%; /* 48px */
      margin-top: 14px;

      &:nth-child(4) {
        margin-top: 0;
        margin-bottom: 9px;
      }
    }

    p {
      color: #2E9164;
      text-align: right;
      font-family: "hand3", sans-serif;
      font-size: 24px;
      font-style: normal;
      font-weight: 500;
      line-height: 150%; /* 36px */
      letter-spacing: -0.264px;
