      max-height: 699px;
    }

    &__notes {
      position: absolute;
      bottom: -24px;
      left: 310px;
    }
  }

  &__text {
    margin-top: 120px;
    color: rgba(51, 51, 51, 0.80);
    font-family: "hand4", sans-serif;
    font-size: 24px;
