  }

  @media (max-width: 1439px) {
    padding: 85px 20px 54px 20px;
    grid-template-columns: repeat(3, 1fr);
    grid-column-gap: 45px;

    &__wrapper {
      svg {
        width: 100%;
      }
    }
  }
}

.for-who {
  padding: 79px 80px;

  &__wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-top: 62px;

    &-problem {
      display: grid;
      grid-template-areas:
        "man text1"
        "man text2"
        "man text3"
        "man text4";
      grid-column-gap: 22px;
      grid-template-rows: repeat(4, fit-content(40%));

      svg {
        grid-area: man;
      }

      p {
        color: rgba(0, 0, 0, 0.80);
        font-family: "RG-Standard", sans-serif;
        font-size: 24px;
        font-style: normal;
        font-weight: 400;
        line-height: 120%; /* 28.8px */

        &:nth-child(2) {
          grid-area: text1;
          color: #000;
          font-size: 36px;
          font-weight: 700;
          line-height: 150%; /* 54px */
        }

        &:nth-child(3) {
          grid-area: text2;
        }

        &:nth-child(4) {
          grid-area: text3;
          align-self: center;
          color: #000;
          font-size: 36px;
          font-weight: 700;
          line-height: 150%; /* 54px */
        }

        &:nth-child(5) {
          grid-area: text4;
        }
      }
    }

    &-decision {
      display: grid;
      grid-row-gap: 28px;
      grid-template-rows: repeat(3, fit-content(40%));
      position: relative;
      padding: 40px 25px 60px 21px;
      background: url("../img/bg-green-ring.png") top left no-repeat;
      background-size: cover;
      width: fit-content;
      justify-self: center;

      svg {
        position: absolute;
        right: -112px;
        top: 10px;
      }

      p {
        font-family: "RG-Standard", sans-serif;
        text-align: center;
        font-size: 24px;
        font-style: normal;
        color: rgba(0, 0, 0, 0.80);
        line-height: 120%; /* 28.8px */
        max-width: 513px;

        &:nth-child(1) {
          color: #2E9164;
          font-size: 36px;
          font-weight: 700;
        }

        &:nth-child(2) {
          font-weight: 400;
        }

        &:nth-child(3) {
          font-weight: 700;
        }
      }
    }
  }

  @media (max-width: 1439px) {
    &__wrapper {
      grid-template-columns: 1fr;
    }
  }
}

.more {
  padding: 0 263px;

  &__wrapper {
    position: relative;
    margin: 110px auto 0 auto;
    display: grid;
    grid-template-areas:
        "one two"
        "three four"
        "five five";

    &-note__1 {
      display: flex;
      align-items: center;
      justify-content: center;
      grid-area: one;

      svg {
        position: absolute;
      }

      p {
        transform: rotate(10.993deg);
        color: rgba(0, 0, 0, 0.80);
        font-family: "Just Breathe", sans-serif;
        font-size: 32px;
        font-style: normal;
        font-weight: 700;
        line-height: 150%; /* 48px */
        letter-spacing: -0.352px;

        span {
          font-weight: 400;
        }
      }
    }

    &-note__2 {
      display: flex;
      align-items: center;
      justify-content: center;
      grid-area: two;

      svg {
        position: absolute;
      }

