      svg {
        position: absolute;
      }

      p {
        transform: rotate(7.706deg);
        color: rgba(0, 0, 0, 0.80);
        font-family: "Just Breathe", sans-serif;
        font-size: 32px;
        font-style: normal;
        font-weight: 700;
        line-height: 150%; /* 48px */
        letter-spacing: -0.352px;

        span {
          display: block;
          margin-top: 60px;
        }
      }
